<template>
  <div class="talion-view">
    <div class="header-bar">
      <span class="close-talion" @click="closeTalion"></span>
      <form class="search" onsubmit="return false">
        <input
          type="search"
          name="query"
          v-model.trim.lazy="queryStr"
          @keyup.enter="goSearch()">
      </form>
    </div>
    <ul class="has-header">
      <li>
        <div>
          <a href="#" target="_blank">
            <strong style="color: rgb(35, 132, 232);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(230, 70, 126);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(159, 120, 96);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(225, 100, 77);"></strong>
            <span></span>
          </a>
        </div>
      </li>
      <li>
        <div>
          <a href="#" target="_blank">
            <strong style="color: rgb(122, 106, 219);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(42, 184, 204);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(87, 116, 197);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(64, 207, 169);">FM</strong>
            <span></span>
          </a>
        </div>
      </li>
      <li>
        <div>
          <a href="#" target="_blank">
            <strong style="color: rgb(159, 120, 96);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(244, 143, 46);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(89, 108, 221);"></strong>
            <span></span>
          </a>
          <a href="#" target="_blank">
            <strong style="color: rgb(66, 189, 86);"></strong>
            <span></span>
          </a>
        </div>
      </li>
    </ul>
    <sub-nav mold="navBottom"></sub-nav>
  </div>
</template>

<script>
  import SubNav from '../components/SubNav'

  export default {
    name: 'talion-view',
    components: { SubNav },
    data () {
      return {
        queryStr: ''
      }
    },
    methods: {
      closeTalion: function () {
        this.$emit('closeTalion')
      },
      /**
       * Going to search view
       * params: q
       */
      goSearch: function () {
        this.$emit('closeTalion')
        this.$router.push({
          name: 'SearchView',
          params: {
            q: this.queryStr
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .talion-view {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 999;
  }

  .close-talion {
    color: #42bd56;
    font-size: 1.6rem;
  }

  .search {
    flex: 1;
    width: 100%;
    padding-left: 1rem;
  }

  input[type=search] {
    box-sizing: border-box;
    border: 0;
    background: url('../assets/search1.png') no-repeat 50% #f3f3f3;
    background-size: 1.73rem;
    border-radius: 0.3rem;
    width: 100%;
    height: 3.2rem;
    font-size: 1.4rem;
  }

  input[type=search]:focus {
    background: #f3f3f3;
  }

  ul {
    display: flex;
    justify-content: space-around;
    padding-top: 2.5rem;

    li {
      width: 33.33%;
    }

    div {
      position: relative;
      float: left;
      left: 50%;
      top: 0;
    }

    a {
      position: relative;
      left: -50%;
      top: 0;
      display: block;
      margin-bottom: 2.5rem;
      text-align: center;
    }

    strong {
      display: block;
      font-weight: 400;
      font-size: 2.4rem;
      line-height: 2.8rem;
    }

    span {
      display: block;
      color: #999;
      height: 1.7rem;
      font-size: 1.2rem;
      letter-spacing: 0.1rem;
    }
  }

</style>



